@import "fontello";

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
body {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  color: #423e37;
  background-color: #e5e5e5;
  // font-family: 'Merriweather', serif;
}
.main {
  flex: 1 0 auto;
}
.footer {
  flex-shrink: 0;
}

img {
  width: 100%;
}

.font-black {
  font-weight: bolder;
}

.main-logo {
  width: 130px;
}
.dev-logo {
  width: 170px;
}
.footer-logo-text {
  color: #fecb81;
  border-left: 1px solid #fecb81;
  padding-left: 15px;
}

.section-title {
  font-size: 44px;
  font-weight: bold;
  position: relative;
  margin-bottom: 1.75rem;

  &--lined {
    &::after {
      content: "";
      position: absolute;
      bottom: -0.75rem;
      left: 0;

      width: 4.375rem;
      height: 2px;
      background: linear-gradient(
        146.41deg,
        #e3be8e 0%,
        #f2c386 54.17%,
        #edb562 100%
      );
      box-shadow: 0px 10px 30px rgba(238, 179, 87, 0.15);
    }
  }
}

.dd-button {
  &-wrap {
    position: relative;
    z-index: 5;
  }

  background: linear-gradient(102.28deg, #fecb81 0%, #ffbd55 100%);
  box-shadow: 0px 10px 30px rgba(238, 179, 87, 0.15);
  border-radius: 10px;
  padding: 20px 80px;
  border: none;
  outline: none !important;
  font-weight: bold;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0%;
    background: linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.2) 26.11%,
      rgba(255, 255, 255, 0) 100%
    );
    width: 0%;
    height: 100%;
    opacity: 0;
    transition: 400ms;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    bottom: -3px;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      104.39deg,
      #b99158 0%,
      #cba060 0.01%,
      #cea96e 100%
    );
    box-shadow: 0px -20px 40px rgba(250, 227, 93, 0.2);
    border-radius: 10px;
  }

  &:hover {
    &::before {
      opacity: 1;
      right: 25%;
      width: 35%;
      transform: skew(-20deg, 0deg);
    }
  }
}

.dd-select {
  &-wrap {
    position: relative;
  }
  width: 100%;
  border: none;
  background: #f9f9f9;
  padding: 12px 15px;
  border-radius: 10px;
  outline: none;
  color: #828282;
  position: relative;

  -webkit-appearance: none;
  -moz-appearance: none;

  &__label {
    text-align: left;
    width: 100%;
    font-weight: bold;
  }
  &__arrow {
    position: absolute;
    top: 20%;
    right: 15px;
    font-size: 20px;
    user-select: none;
    pointer-events: none;
  }
}

.dd-accordion {
  padding: 15px 30px;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 20px;

  &.open {
    .dd-accordion__content {
      padding: 15px 0;
      height: auto;
    }
    .dd-accordion__header-button {
      border-bottom: 1px solid #e0e0e0;
    }
    .dd-accordion__content-text {
      opacity: 1;
      user-select: all;
    }
  }

  &__header {
    position: relative;
    &-button {
      color: #423e37;
      width: 100%;
      background: none;
      border: none;
      text-align: left;
      outline: none !important;
      font-weight: bold;
      padding: 12px 0;
    }
    &-arrow {
      position: absolute;
      right: 15px;
      top: 20%;
      font-size: 20px;
      user-select: none;
      pointer-events: none;
    }
  }
  &__content {
    color: #828282;
    height: 0;
    padding: 0;
    transition: 600ms;

    &-text {
      opacity: 0;
      user-select: none;
    }
  }
}

.service-card {
  background: linear-gradient(62.95deg, #3f3138 0%, #564042 74.65%);
  border-radius: 10px;
  border: 5px solid transparent;
  padding: 35px 50px;
  transition: 400ms;
  height: 260px;
  opacity: 0.8;
  margin-bottom: 2.5rem;
  position: relative;

  &__content {
    width: 50%;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 12px;
  }

  &__image {
    position: absolute;
    width: 50%;
    right: -5px; // border width
    bottom: -5px; // border width
  }
  &__title {
    font-size: 1.625rem;
    color: #fff;
    font-weight: bold;
  }
  &__link {
    text-decoration: none !important;
  }
  &__link-label {
    color: #fff;
    &:hover {
      color: #fff;
    }
  }

  &:hover {
    border-color: #ffbd55;
    opacity: 1;
  }

  &--right {
    .service-card__content {
      margin-left: auto;
    }
    .service-card__image {
      width: 50%;
      left: -5px; // border width
    }
  }
}

.team-card {
  margin-bottom: 1.75rem;
  filter: grayscale(0.3);
  transition: 400ms;

  &__image {
    margin-bottom: 0.5rem;
  }
  &__name {
    font-size: 1.125rem;
    font-weight: bold;
  }
  &__position {
    font-size: 0.875rem;
  }

  &:hover {
    filter: grayscale(0);
  }
}

.benefit-card {
  width: 240px;
  text-align: center;
  margin-bottom: 4rem;
  &__image {
    width: 4.5rem;
  }
  &__title {
    font-size: 1.125rem;
    font-weight: bold;
  }
  &__text {
    font-size: 1rem;
    color: #f5f0ed;
  }
}

.property-card {
  background: #fff;
  border-radius: 10px;
  width: 100%;
  transition: 600ms;

  &__gallery {
    border-radius: 10px 10px 0 0;
    width: 100%;

    &-lens {
      opacity: 0;
      transition: 400ms;

      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 5;
      color: #fff;
      background: rgba(0, 0, 0, 0.8);
      pointer-events: none;
      user-select: none;
      padding: 8px 12px;
      border-radius: 10px;
      transform: translate(-50%, -50%);
    }

    &-pagination {
      // transition: 400ms;
      // opacity: 1;

      background: rgba(0, 0, 0, 0.8);
      width: 130px !important;
      border-radius: 12px;
      padding: 12px;
      left: 50% !important;
      transform: translateX(-50%);

      .swiper-pagination-bullet {
        background: #fff;
        opacity: 1;
        margin: 0 4px;
      }
      .swiper-pagination-bullet-active {
        background: #fecb81;
      }

      .pag-bullets {
        line-height: 1px;
      }
      .pag-fraction {
        line-height: 24px;
        color: #fff;
        font-weight: bold;
        span {
          color: #fecb81;
        }
      }
    }

    &-next {
      transition: 400ms;
      opacity: 0;

      &.swiper-button-disabled {
        display: none;
      }

      &::after {
        color: #fff;
        background: rgba(0, 0, 0, 0.8);
        padding: 8px 12px;
        padding-left: 14px;
        border-radius: 50%;
        font-size: 12px;
        font-weight: bold;
      }
    }
    &-prev {
      transition: 400ms;
      opacity: 0;

      &.swiper-button-disabled {
        display: none;
      }

      &::after {
        color: #fff;
        background: rgba(0, 0, 0, 0.8);
        padding: 8px 12px;
        padding-right: 14px;
        border-radius: 50%;
        font-size: 12px;
        font-weight: bold;
      }
    }
  }

  &__body {
    padding: 20px;
    &-title {
      padding-bottom: 15px;
      margin: 0;
      font-weight: bold;
      font-size: 1.25rem;
      border-bottom: 1px solid #e0e0e0;
    }

    &-content {
      display: flex;
      flex-wrap: wrap;
      padding-top: 15px;
    }
    &-parameter {
      width: 50%;
      margin-bottom: 15px;
    }
  }

  &:hover {
    box-shadow: 10px 10px 50px rgba(133, 133, 133, 0.2);
    transform: translateY(-6px);

    .property-card__gallery-lens {
      opacity: 1;
    }
    .property-card__gallery-pagination {
      opacity: 1;
    }
    .property-card__gallery-next,
    .property-card__gallery-prev {
      opacity: 1;
    }
  }
}

.contract-link {
  font-weight: bold;
  color: #fff;
  i {
    font-size: 20px;
  }
}

.medium-icon {
  font-size: 20px;
}

.main-header {
  position: absolute;
  width: 100%;
  left: 0;
  z-index: 100;
  color: #fff;

  &__phone {
    font-size: 1.625rem;
    font-weight: bold;
  }
  &__whatsapp {
    font-weight: bold;
    padding: 10px 30px;
    color: #fff !important;
    text-decoration: none !important;
    border: 1px solid #fff;
    border-radius: 10px;
    display: inline-block;

    i {
      font-size: 24px;
    }
  }

  .navbar-collapse {
    align-items: flex-start;
    margin-top: 2rem;
  }

  .nav-item {
    .nav-link {
      color: #fff;
    }
    &.active {
      .nav-link {
        color: #fecb81;
        position: relative;
        &::after {
          content: "";
          position: absolute;
          left: 0.5rem;
          bottom: 0;
          width: 50px;
          height: 1px;
          background: linear-gradient(99.36deg, #fecb81 0%, #ffbd55 100%);
        }
      }
    }
  }
}

.main-footer {
  background-image: url("../assets/footer_bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  padding: 50px 0;
  color: #fff;

  hr {
    border-color: #828282;
  }

  &__social-icon {
    color: #fff !important;
    font-size: 30px;
    transition: 800ms;
    &:hover {
      opacity: 0.6;
    }
  }

  &__title {
    font-weight: bold;
    position: relative;
    display: inline-block;

    &::after {
      content: "";
      position: absolute;
      right: -65px;
      top: 50%;
      width: 50px;
      height: 1px;
      background: #fff;
    }
  }

  &__nav {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;

    li {
      width: 50%;
      margin-bottom: 15px;
    }
    a {
      color: #fff !important;
      font-size: 14px;
      // transition: 400ms;
      &:hover {
        text-decoration: none;
      }
      &.active,
      &:hover {
        font-weight: bold;
      }
    }
  }

  &__contacts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    &-item {
      margin-bottom: 10px;
    }
  }

  &__copyright {
    color: #bdbdbd;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    &-item {
      margin-bottom: 10px;
    }
  }
}

.floating-items {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  user-select: none;
  pointer-events: none;

  &__item-1 {
    position: absolute;
    top: -150px;
    width: 300px;
    left: 0;
  }
  &__item-2 {
    position: absolute;
    top: 250px;
    width: 180px;
    right: 0;
  }
  &__item-3 {
    position: absolute;
    top: 600px;
    width: 180px;
    right: 0;
    z-index: 10;
  }
}

.income-calculator {
  max-width: 770px;
  background: #fff;
  border-radius: 10px;

  &__header {
    background: linear-gradient(
      105.81deg,
      #b99158 0%,
      #cba060 0.01%,
      #cea96e 100%
    );
    border-radius: 10px 10px 0px 0px;
    padding: 20px 5px;
    font-size: 2.75rem;
    color: #fff;
    font-weight: bold;
  }

  &__body {
    padding: 30px 75px;
    box-shadow: 10px 10px 40px rgba(188, 188, 188, 0.2);
    &-title {
      text-align: center;
      color: #828282;
    }
  }

  &__footer {
    padding: 30px 75px;

    &-title {
      margin-bottom: 0;
    }

    &-placeholder {
      font-size: 1.625rem;
      font-weight: bold;
    }

    &-result {
      font-size: 1.625rem;
      font-weight: bold;
      display: none;
    }
  }
}

