@font-face {
  font-family: "fontello";
  src: url("../fonts/fontello/fontello.eot?50099780");
  src: url("../fonts/fontello/fontello.eot?50099780#iefix")
      format("embedded-opentype"),
    url("../fonts/fontello/fontello.woff2?50099780") format("woff2"),
    url("../fonts/fontello/fontello.woff?50099780") format("woff"),
    url("../fonts/fontello/fontello.ttf?50099780") format("truetype"),
    url("../fonts/fontello/fontello.svg?50099780#fontello") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
